



.main-content  {
    animation: fadeIn 3s ease;

    overflow-x: hidden;
    background-color: #bfc6ff; /* Fallback color */
    background: linear-gradient(to top right, #bfc6ff 45%, white 90%);
}


@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    40% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


.website-bg  {
    background-image: url('../portfolio/images/Creations/creations.jpg');
    height: 45vh;
    /* background-size: cover; */
