.tap-button {
    border: 1px solid #301934;
    letter-spacing: 1px;
    padding: 7px 20px;
    background-color: #2f2f8e;
    color: white;
}


.tap-button:hover {
    border: 1px solid #000000;
    letter-spacing: 1px;
    padding: 7px 20px;
    background-color: transparent;
    color: black;
    transition: 0.4s;
}


.untap-button {
    border: 1px solid #301934;
    letter-spacing: 1px;
    padding: 7px 20px;
    background-color: transparent;
    color: black;
}


.untap-button:hover {
    border: 1px solid #000000;
    letter-spacing: 1px;
    padding: 7px 20px;
    background-color: #2f2f8e;
    color: white;
    transition: 0.4s;
}





.heading-underline {
    position: relative;
    display: inline-block;
  }
  

  
  .heading-underline::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -5px; /* Adjust based on your preference */
    width: 100%;
    height: 1px; /* Adjust based on your preference */
    background-color: #2f2f8e; /* Adjust based on your preference */
  }
  

 

  .recruitment-points  {
    list-style: none; 
    position: relative; 
  
  }
  

.recruitment-points ::before {
  content: '☑️ '; 
  color: blue; 
  position: absolute; 
  left: -0.1em; 
}
  


.box-skills {
    width: 5rem;
    border: 1px solid black;
    border-radius: 6px;
    padding: 8px;
    background-color: #2f2f8e;
    
}


.symbols-together   {
    display: flex;
    gap: 8px;
   
}


.box-vertical {
    border: 0px solid #ddd;
    border-radius: 0px;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #9a9fe500;
    display: flex;
    flex-direction: column;
    /* height: 100%; */
}


/* Media query for devices between 780px and 971px */


.service-images {
