



.bio-bg {

    overflow-x: hidden;
    background-color: #bfc6ff; /* Fallback color */
    background: linear-gradient(to top right, #bfc6ff 45%, white 90%);

}



.about-box  {
    border:1px solid black;
    padding: 20px;
    height: 20rem;
    background-color: #bfc6ff;
    margin-left: 10px;
   
}


.about-img {
    position: relative;
    z-index: 1;
}

.about-contents {
    position: relative;
    z-index: 2;
    padding: 2rem;
    background-color: aliceblue;
}


.box-container {
    border: 2px solid rgba(0, 0, 0, 0.2); /* Light border */
    border-radius: 8px; /* Rounded corners */
    background-color: rgba(255, 255, 255, 0.8); /* Semi-transparent background */
    padding: 1rem; /* Padding inside the box */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow effect */
    max-width: 50%; /* Ensure it doesn't overflow */
    margin: 0 auto; /* Centering the box */
  }


  .company-images {
    width: 50%;
    height: 10rem;
    border-radius: 10px;
   
}



.epm-card {
    background: #00000047;
    border-radius: 16px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 10px;
    /* margin-bottom: 20px; */
    transition: transform 0.3s ease;
    /* height: 10rem; */
}


.epm-card:hover {
    transform: scale(1.05);
}

.epm-images {
    width: 3rem;
    margin-bottom: 19px;
    height: 3rem;
}


