
@font-face {
    font-family: 'Josefin Slab';
    src: url('/src/spartan/Spartan-Light.ttf') format('woff2'),
         url('/src/gilroy-bold/Gilroy-Medium.ttf') format('woff');
    font-weight: bold;
    font-style: normal;
}


.test {
    font-family: 'Josefin Slab';
    text-align: justify;
  
}


.name-position {
    justify-content: left;
}

.name-position h1,
.name-position p {
    margin: 0; 
}


.name-position h1 {
    margin-bottom: 0px; 
}




.hello {
    position: relative;
    display: inline-block;
}


.hello:before {
    content: '';
    position: absolute;
    left: 10;
    bottom: -2px;
    width: 14rem;
    height: 2px;
    background-color: #c52020; 
}


.home-content {
    min-height: 100vh;
    overflow-x: hidden;
    background-color: #bfc6ff; /* Fallback color */
    background: linear-gradient(to top right, #bfc6ff 45%, white 90%);
}



label {
    margin-top: 10px;
    font-size: 14px;
    color: #333;
}

input[type='text'] {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    outline: none;
    font-size: 16px;
    padding: 5px 0;
    margin-top: 5px;
    margin-bottom: 15px;
    width: 100%;
    transition: border-color 0.3s;
}

input[type='text']:focus {
    border-bottom-color: blue;
}


.social-images {
    width: 2rem;
    margin-right: 12px;
    cursor: pointer;
}




.profile-pic  {
    width: 23rem;
    
   
    
}


@media (max-width: 768px) {
    .profile-pic  {
        width: 20rem;
    }

}


.mobile-social-media {
    list-style: none;
    display: flex;

}

@media (min-width: 768px) {
    .mobile-social-media {
       display: none;
    }

}




.about-bg {
    background-color: #bfc6ff; /* Fallback color */
    background: linear-gradient(to bottom right, #99a5ee 45%, white 90%);
    height: 25vh;
    /* background-size: cover; */
    background-position: center;
    /* background-repeat: no-repeat; */
    background-attachment: fixed;
}