


* {
    font-family: 'Poppins', sans-serif; 
    margin: 0;
    padding: 0;
    letter-spacing: 1px;

    
}


@font-face {
    font-family: 'Poppins';
    src: url('/src/Poppins/Poppins-Light.ttf') format('woff2'),
         url('/src/gilroy-bold/Gilroy-Light.ttf') format('woff');
    font-weight:bolder;
    font-style: normal;
    
}


header  {
    position: fixed;
    color: white;
    top: 0;
    left: 0;
    width: 100%;
    padding: 30px ;
    background-color: #2f2f8e;
    z-index: 1;
}


.nav-lists a {
    color: white; /* Change this to match your design */
    text-decoration: none;
}

.Logo-color {
    color: white; /* Change this to match your design */
    text-decoration: none;

}

.nav-contents {
    text-decoration: none; 

}


.nav-contents .nav-lists {
    display: flex;
    list-style: none; 
    position: absolute;
    left: 0;
    
}


.nav-lists li {
    position: relative;
    cursor: pointer;
    margin-top: 15px;
    margin-right: 40px;
    font-size: 0.9rem;
    transition: 1s;
   
}
    


.nav-lists li:hover {
    cursor: pointer;
    color: yellow;
}



.footer-bg {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    height: 3rem;   
    background-color: #2f2f8e;
}