

.sidebar {
    background-color: #2f2f8e;
    position: fixed; /* Keeps the sidebar fixed as user scrolls */
    top: 20rem; /* Aligns sidebar to top */
    right: 0; /* Aligns sidebar to left */
    overflow-x: hidden; /* Ensures content doesn't overflow horizontally */
    padding: 1.5rem 0rem;
}



@media (max-width: 760px)  {

    .sidebar {
        display: none;
    }

}




.social-images  {
    width: 2rem;
  
}

.social-images:hover {
    cursor: pointer;
    transform: translateX(-3px);
  
}


.sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 10px 0px;
   
}

.sidebar ul li {
    padding: 10px;

}

.sidebar ul li a {
    text-decoration: none;
    color: #333;
}

.sidebar ul li a:hover {
    color: #000;
    background-color: #ccc;
}
